* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  scroll-behavior: smooth;
  -webkit-tap-highlight-color: transparent;
  background-color: #000;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
  overflow-x: hidden;
}

.nav-wrapper {
  z-index: 1000;
  align-items: center;
  display: flex;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.side-button {
  color: #fff;
  cursor: pointer;
  box-shadow: none;
  text-align: center;
  touch-action: manipulation;
  background: none;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 90px;
  padding: 12px 15px;
  font-size: 14px;
  transition: all .3s;
  display: flex;
  position: relative;
  left: 20px;
}

.side-button:hover {
  background: none;
  transform: translateY(-50%)scale(1.05);
}

.logo-image {
  object-fit: contain;
  border-radius: 6px;
  width: 80px;
  height: 80px;
  margin-bottom: 2px;
  animation: 2s infinite pulse;
  position: relative;
}

@keyframes pulse {
  0% {
    filter: drop-shadow(0 0 #fc0);
  }

  50% {
    filter: drop-shadow(0 0 10px #fc0);
  }

  100% {
    filter: drop-shadow(0 0 #fc0);
  }
}

.side-button:before {
  display: none;
}

@keyframes shine-left {
  0% {
    left: -100%;
  }

  20% {
    left: 150%;
  }

  100% {
    left: 150%;
  }
}

.nav-buttons {
  -webkit-backdrop-filter: blur(10px);
  opacity: 0;
  visibility: hidden;
  background: #000000b3;
  border-radius: 8px;
  flex-direction: column;
  gap: 10px;
  width: 400px;
  padding: 20px;
  transition: all .3s;
  display: flex;
  position: relative;
  left: 30px;
  transform: translateX(-20px);
  box-shadow: 0 4px 12px #0000004d;
}

.nav-buttons.active {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.game-buttons {
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  width: 100%;
  margin-bottom: 20px;
  display: grid;
}

.game-button {
  cursor: pointer;
  background: #282828e6;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  transition: all .3s;
  display: flex;
}

.game-button:hover {
  background: #3c3c3ce6;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px #0000004d;
}

.game-logo {
  object-fit: contain;
  width: 60px;
  height: 60px;
  margin-bottom: 8px;
}

.game-button span {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}

.vip-channel {
  width: 100%;
  margin-top: 10px;
}

.vip-button {
  color: #000;
  cursor: pointer;
  text-align: center;
  background: linear-gradient(135deg, #d4af37, #f9d423);
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 15px;
  font-size: 18px;
  font-weight: bold;
  transition: all .3s;
  box-shadow: 0 4px 8px #0000004d;
}

.vip-button:hover {
  background: linear-gradient(135deg, #f9d423, #d4af37);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px #0006;
}

.vip-button.btn-clicked {
  transform: scale(.95);
}

.route-item {
  background: none;
  border: 1px solid #ffcc004d;
  border-radius: 5px;
  align-items: center;
  gap: 15px;
  width: 100%;
  padding: 12px;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #ffcc001a;
}

.route-item:hover {
  background: #3c3c3c80;
  border: 1px solid #fc09;
  box-shadow: 0 0 15px #fc03;
}

.route-item:before {
  content: "";
  background: linear-gradient(to right, #0000, #fc03, #0000);
  width: 50%;
  height: 100%;
  animation: 3s infinite routeShine;
  position: absolute;
  top: 0;
  left: -100%;
  transform: skewX(-25deg);
}

@keyframes routeShine {
  0% {
    left: -100%;
  }

  20%, 100% {
    left: 200%;
  }
}

.speed {
  text-align: center;
  color: #fc0;
  text-shadow: 0 0 5px #ffcc0080;
  background: none;
  border-radius: 4px;
  min-width: 70px;
  padding: 5px 10px;
  font-weight: bold;
  animation: 2s infinite pulsateText;
}

@keyframes pulsateText {
  0%, 100% {
    text-shadow: 0 0 5px #ffcc0080;
  }

  50% {
    text-shadow: 0 0 15px #fc0c;
  }
}

.route-name {
  text-align: center;
  color: #fff;
  text-shadow: 0 0 8px #ffffff80;
  flex-grow: 1;
  font-size: 18px;
  font-weight: bold;
}

.download-btn {
  color: #fff;
  cursor: pointer;
  text-shadow: 0 1px 2px #00000080;
  z-index: 1;
  text-align: center;
  background: linear-gradient(135deg, #8b4513, sienna);
  border: none;
  border-radius: 4px;
  padding: 10px 25px;
  font-size: 16px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 10px #a0522d80;
}

.download-btn:after {
  content: "";
  opacity: 0;
  z-index: -1;
  background: radial-gradient(circle, #ffffff4d 0%, #0000 70%);
  width: 200%;
  height: 200%;
  transition: opacity .3s;
  position: absolute;
  top: -50%;
  left: -50%;
}

.download-btn:hover {
  background: linear-gradient(135deg, sienna, #8b4513);
  transform: translateY(-2px);
  box-shadow: 0 0 15px #a0522db3;
}

.download-btn:hover:after {
  opacity: .5;
  animation: 1.5s infinite btnGlow;
}

@keyframes btnGlow {
  0%, 100% {
    opacity: .3;
  }

  50% {
    opacity: .6;
  }
}

.web-version {
  background: #8b4513;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  animation: 1s ease-in-out fadeIn;
  overflow-x: hidden;
}

.image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  animation: 6s ease-in-out infinite floatContainer;
  display: flex;
  position: relative;
}

.main-image {
  object-fit: contain;
  opacity: 0;
  filter: drop-shadow(0 0 8px #ffcc004d);
  border-radius: 12px;
  width: 1000px;
  max-width: 100%;
  height: 2800px;
  transition: all .5s;
  animation: 1.5s forwards fadeInAndGlow;
  box-shadow: 0 10px 25px #fc03;
}

.main-image:hover {
  filter: drop-shadow(0 0 12px #ffcc0080);
  transform: translateY(-8px)scale(1.02);
  box-shadow: 0 15px 30px #ffcc004d;
}

@keyframes fadeInAndGlow {
  0% {
    opacity: 0;
    filter: brightness(.8) drop-shadow(0 0 #fc00);
    transform: translateY(30px)scale(.95);
  }

  60% {
    opacity: 1;
    filter: brightness(1.1) drop-shadow(0 0 15px #fc09);
    transform: translateY(0)scale(1);
  }

  80% {
    filter: brightness(1.05) drop-shadow(0 0 10px #fc06);
  }

  100% {
    opacity: 1;
    filter: brightness() drop-shadow(0 0 8px #ffcc004d);
    transform: translateY(0)scale(1);
  }
}

@keyframes floatContainer {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

@media (width <= 768px) {
  .main-image {
    width: 100%;
    max-width: 600px;
    height: auto;
  }

  .container {
    padding: 10px;
  }

  .nav-buttons {
    width: 320px;
    padding: 20px;
  }

  .nav-wrapper {
    justify-content: flex-start;
    width: auto;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  .side-button {
    width: 120px;
    margin: 0;
    padding: 18px;
    font-size: 18px;
    left: 15px;
  }

  .logo-image {
    width: 80px;
    height: 80px;
  }

  .nav-buttons {
    width: 280px;
    margin-left: 10px;
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translateY(-50%)translateX(-20px);
  }

  .nav-buttons.active {
    transform: translateY(-50%)translateX(0);
  }

  .route-item {
    flex-wrap: wrap;
    gap: 12px;
    padding: 15px;
  }

  .download-btn {
    -webkit-appearance: none;
    -webkit-touch-callout: none;
    touch-action: manipulation;
    -webkit-user-select: none;
    user-select: none;
    border-radius: 6px;
    width: 100%;
    margin-top: 8px;
    padding: 15px 25px;
    font-size: 18px;
    display: block;
  }

  .download-btn:active {
    background: linear-gradient(135deg, sienna, #8b4513);
    transform: scale(.98);
  }

  .speed, .route-name {
    font-size: 18px;
  }

  .side-button, .download-btn, .game-button, .vip-button {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
  }
}

@media (width <= 480px) {
  .nav-buttons {
    width: 250px;
    max-width: 90vw;
  }

  .side-button {
    width: 110px;
    padding: 15px;
    font-size: 16px;
  }

  .logo-image {
    width: 70px;
    height: 70px;
  }

  .download-btn {
    padding: 14px 22px;
    font-size: 17px;
  }

  .copyright {
    padding-bottom: 10px;
    font-size: 10px;
  }
}

@media (height <= 500px) and (orientation: landscape) {
  .nav-wrapper {
    width: auto;
    top: 50%;
    bottom: auto;
    left: 10px;
    transform: translateY(-50%);
  }

  .side-button {
    transform: none;
  }

  .side-button:hover {
    transform: scale(1.05);
  }

  .nav-buttons {
    top: 50%;
    bottom: auto;
    left: 100%;
    transform: translateY(-50%)translateX(-10px);
  }

  .nav-buttons.active {
    transform: translateY(-50%)translateX(0);
  }

  .main-image {
    width: auto;
    height: 90vh;
    max-height: 1200px;
  }
}

.loader {
  z-index: 2000;
  opacity: 1;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  border: 5px solid #8b45134d;
  border-top-color: #8b4513;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
  animation: 1s ease-in-out infinite spin;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.copyright {
  text-align: center;
  color: #fff9;
  width: 100%;
  padding: 5px 0;
  font-size: 12px;
  position: fixed;
  bottom: 10px;
  left: 0;
}

.side-button.clicked {
  background: none;
  transform: translateY(-50%)scale(.95);
}

.download-btn.btn-clicked {
  background: linear-gradient(135deg, sienna, #8b4513);
  transform: scale(.95);
  box-shadow: 0 0 8px #a0522d66;
}

:focus {
  outline-offset: 2px;
  outline: 2px solid #fc0;
}

:focus:not(:focus-visible) {
  outline: none;
}

.route-name, .copyright, .side-button div {
  text-shadow: 0 1px 2px #000c;
}

.download-btn:hover, .download-btn:focus {
  background: sienna;
  transform: scale(1.05);
  box-shadow: 0 4px 12px #0000004d;
}
